.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    color: white !important;
}

.rightbarProfileImgContainer
{
    margin-right: 10px;
    position: relative;

}

.rightbarProfileImg{
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
}

.rightbarOnline{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: rgb(32, 255, 32);
    position: absolute;
    top: -2;
    right: 0;
    border: 2px solid white;
}

.rightbarUsername{
    font-weight: 500;

}