.sidebar{

    flex: 3;
    /* height:calc( 100vh-50px); */
   overflow-y:scroll;
   height: vh;
   
height: 90vh;
/* chnages */
background-color:rgb(24, 26, 27)!important ;
color:white;
font-weight: 800;
border-radius:10px;

/* opacity:0.8; */
cursor: pointer;
border-right: 0.1px solid rgb(49, 48, 48);
}

::-webkit-scrollbar{

    width: 5px;
   
}
::-webkit-scrollbar-track{

   
    background-color: rgb(240, 237, 237);
}

::-webkit-scrollbar-thumb{

  
    
    background-color: rgb(184, 184, 184);
}


.sidebarWrapper{
 padding: 20px;
    
}

.sidebarList{
    padding: 0;
    margin: 0;
    list-style: none;

}
.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom: 29px;
}

.sidebarIcon{
    margin-right: 15px;
}

.sidebarButton{
    width: 150px;
    border: none;
    padding: 10px;

    border-radius: 5px;
    font-weight: 500;


}
.sidebarHr{
    margin: 20px 0px;

}

.sidebarfriendList{
    padding: 0;
    margin: 0;list-style: none;
    display: flex;
    flex-direction: column;

}
.Recommendationtext{
    margin-top:5px;
    padding-bottom:15px;
    width: 100%;
    /* background-color: red; */
}

.focusTab{
    background-color:rgb(84, 84, 118);
    width:50%;
    padding: 10px;
}
@media screen and (max-width: 600px ) {
    .focusTab{
      
        width:70%;
        padding: 10px;
